.calendar-skin {
	font-size: 90%;
}

.calendar-skin .ui-widget {
	background: $p-d-c;
	width: auto !important;
	border: none;
	border-radius: 0;
	-webkit-border-radius: 0;
	-moz-border-radius: 0;
}

.calendar-skin .ui-datepicker {
	padding: 0;
}

.calendar-skin .ui-datepicker-header {
	border: none;
	background: $t-l-c;
	font-weight: normal;
	font-size: 15px;
}

.calendar-skin .ui-datepicker-header .ui-state-hover {
	background: transparent;
	border-color: transparent;
	cursor: pointer;
	border-radius: 0;
	-webkit-border-radius: 0;
	-moz-border-radius: 0;
}

.calendar-skin .ui-datepicker .ui-datepicker-title {
	margin-top: .4em;
	margin-bottom: .3em;
	color: #e9f0f4;
}

.calendar-skin .ui-datepicker .ui-datepicker-prev-hover,
.calendar-skin .ui-datepicker .ui-datepicker-next-hover,
.calendar-skin .ui-datepicker .ui-datepicker-next,
.calendar-skin .ui-datepicker .ui-datepicker-prev {
	top: .9em;
	border:none;
}

.calendar-skin .ui-datepicker .ui-datepicker-prev-hover {
	left: 2px;
}

.calendar-skin .ui-datepicker .ui-datepicker-next-hover {
	right: 2px;
}

.calendar-skin .ui-datepicker .ui-datepicker-next span,
.calendar-skin .ui-datepicker .ui-datepicker-prev span {
	background-image: url('../../images/calendar-arrows.png');
	background-position: -32px 0;
	margin-top: 0;
	top: 0;
	font-weight: normal;
}

.calendar-skin .ui-datepicker .ui-datepicker-prev span {
	background-position: -96px 0;
}

.calendar-skin .ui-datepicker table {
	margin: 0;
	thead {
		background-color: $t-l-c;
	}
}

.calendar-skin .ui-datepicker th {
	padding: 1em 0;
	color: #ccc;
	font-size: 13px;
	font-weight: normal;
	border: none;
	border-top: 1px solid #3a414d;
}

.calendar-skin .ui-datepicker td {
	background: $p-l-c;
	border: none;
	padding: 0;
}

.calendar-skin td .ui-state-default {
	background: transparent;
	border: none;
	text-align: center;
	padding: .5em;
	margin: 0;
	font-weight: normal;
	color: $p-d-c;
	font-size: 16px;
}

.calendar-skin .ui-state-disabled  {
	opacity: 1;
}

.calendar-skin .ui-state-disabled .ui-state-default {
	color: $p-d-c;
	background-color: $p-l-c;
	opacity: .3;
}

.calendar-skin td .ui-state-active,
.calendar-skin td .ui-state-hover {
	background: $t-l-c;
}